<template>
    <div>
        <div style="position:absolute;z-index:-1;top:-1000px;">
            <audio controls id="audioIn">
                <source src="@/static/welcome.mp3" type="audio/mpeg">
                Your browser does not support the audio element.
            </audio>
            <audio controls id="audioOut">
                <source src="@/static/tts.mp3" type="audio/mpeg">
                Your browser does not support the audio element.
            </audio>
        </div>
        <div class="row justify-content-center p-5">
            <div class="col-10 p-1">
                <div class="shadow p-4 rounded-sm">
                    <div class="text-lg font-semibold text-center" @click="getRandom">
                        Voice : 
                        <span class="font-bold " :class="startAgain?'text-green-500':'text-red-500'">
                            {{startAgain?'ON':'OFF'}}
                        </span>
                    </div>
                    <hr>
                    <div class="row">
                        <div class="col-6 text-center text-xl font-bold ">
                            <div>
                                <div class="bg-blue-400 p-5 rounded-lg shadow-lg">
                                    Visitor IN : <span class="animate__animated animate__bounceInDown">{{datanya.totalIn}}</span>
                                </div>
                            </div>
                            <div class="mt-4">
                                <div class="bg-red-400 p-5 rounded-lg shadow-lg">
                                    Visitor OUT : <span class="animate__animated animate__bounceInDown">{{datanya.totalOut}}</span>
                                </div>
                            </div>
                        </div>
                        <div class="col-6 text-center text-xl font-bold ">
                            <div id="chartLocationVisitor">

                            </div>
                        </div>
                    </div>
                    <hr>
                    <div class="row">
                        <div class="col-12">
                            <hr>
                            <p @click="resetCount">Location Table</p>
                            <div class="table-responsive" >
                                <table class="table table-sm table-bordered table-striped">
                                    <tr>
                                        <td>Nama Lokasi</td>
                                        <td class="font-bold">Jumlah</td>
                                    </tr>
                                    <tr v-for="(item,keys, index) in location" :key="index+'keys'">
                                        <td class="">{{keys}}</td>
                                        <td class="font-bold">{{item}}</td>
                                    </tr>
                                </table>
                            </div>
                        </div>
                    </div>
                </div> 
            </div>
        </div>
        <hr>
    </div>
</template>
<script>
var chartLocation=false;
export default{
    data(){
        return{
            datanya:[],
            location:{
            },
            keys:[],
            in:0,
            out:0,
            startAgain:true,
            wait:true,
            first:0
        }
    },
    methods: {
        resetCount(){
            let data={
                totalIn:0,
                totalOut:0
            }
            axios.put("https://islamicapp-4deac-default-rtdb.asia-southeast1.firebasedatabase.app/visitor.json",data)
        },
        updateLocation(){
            let that=this;
            let arr=[{data:[]}]
            that.keys.forEach((e,i)=>{
                arr[0]['data'][i]=that.location[e]
            })
            console.log(arr)
            chartLocation.updateSeries(arr)
        },
        getRandom(){
            function randomize() {
                return chartLocation.w.globals.series.map(function() {
                    return {
                        data:[
                            Math.floor(Math.random() * 20) + 1,Math.floor(Math.random() * 20) + 1
                        ]
                    }
                })
            }
            console.log(randomize())
            chartLocation.updateSeries(randomize())
        },
        getLocationVisitorChart(){
            let that=this;
             var options = {
                series: [
                {
                    data: [0,0]
                }
                  ],
            chart: {
                type: 'bar',
                height: 350
            },
            plotOptions: {
                bar: {
                    borderRadius: 4,
                    horizontal: true,
                }
            },
             colors: ['#33b2df', '#546E7A', '#d4526e', '#13d8aa', '#A5978B', '#2b908f', '#f9a3a4', '#90ee7e',
                '#f48024', '#69d2e7'
            ],
            dataLabels: {
                enabled: false
            },
            xaxis: {
            categories: that.keys,
            }
            };
            that.keys.forEach((e,i)=>{
                options['series'][0]['data'][i]=that.location[e]
            })
            chartLocation = new ApexCharts(document.querySelector("#chartLocationVisitor"), options);
            chartLocation.render();
            
        },
    },
    layout:'visitor',
    mounted(){
        let that=this;
        let audioIn=document.getElementById('audioIn')
        let audioOut=document.getElementById('audioOut')
        const db = firebase.getDatabase();
        const starCountRef = firebase.ref(db, 'visitor/');
        firebase.onValue(starCountRef, (snapshot) => {
            if(that.wait){
                setTimeout(() => {
                    that.startAgain=true
                    that.wait=true;
                    console.log("Start AGAIN")
                }, 20000);
            }
            const data = snapshot.val();
            that.datanya=data;
            if(that.first==0){
                that.in=data.totalIn
                that.out=data.totalOut
                // audioIn.play()
            }
            if(that.startAgain){
                that.wait=false;
            }
            that.first++
            if(that.in!=data.totalIn){
                that.in=data.totalIn
                console.log('in')
                if(that.startAgain){
                    audioIn.play()
                }
                that.startAgain=false;
            }
            if(that.out!=data.totalOut){
                that.out=data.totalOut
                console.log('out')
                if(that.startAgain){
                    audioOut.play()
                }
                that.startAgain=false;
            }
        });

        const locationRef = firebase.ref(db, 'location/');
        firebase.onValue(locationRef, (snapshot) => {
            const data = snapshot.val();
            that.location=data
            that.keys=Object.keys(data)
            if(chartLocation){
                that.updateLocation()
            }else{
                that.getLocationVisitorChart()
            }
        });
    }
}
</script>